.button{
  background: $blue;
  color: white;
  border: none;
  font-weight: 300;
  font-size: $font-size-large;
  padding: $s-size;
  display: inline-block;
  text-decoration: none;
  line-height: 1;
}

.button--link {
  background: none;
}

.button--secondary {
  background: #888;
  margin-bottom: $s-size;
}


